.Messages {
	background: #252525;
	height: 90vh;
	p {
		color: #fff;
		margin: 5px 0;
		a {
			color: #fff; } }
	h2 {
		text-align: center;
		color: #FF7400;
		padding: 10px 0; } }
.dialogs {
	display: flex; }
.dialogs__users {
	border-right: 2px solid #FF7400;
	width: 30%;
	margin: 0 10px; }
.dialogs__messages {
	width: 70%;
	div {
		margin-left: 15px; }
	position: relative; }
.dialogs__messages-pos {
	position: absolute;
	bottom: 0; }
